@mixin mediaQuery($width: 940px) {
    @media screen and (min-width: $width) {
        @content;
    }
}

@mixin imgFit($width: 1rem, $height: 1rem) {
    width: $width;
    height: $height;
    object-fit: cover;
}

@mixin centerall (){
    display:flex;
    align-items:center;
    justify-content: space-between;
}

@mixin gridEven($cols,$gap){
    display:grid;
    grid-template-columns: repeat($cols,1fr);
    grid-gap:$gap;

}